.container {
    width: 80%;
    height: 400px;
    background-color: #928f8c;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.box {
    width: 200px;
    height: 200px;
    background: #686dff;
    border-radius: 5px;
    border: 2px solid;
    display: flex;
    flex-direction: column;
    padding: 15px;
    justify-content: space-around;
}


.display {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: 5px;
    border: 2px solid;
    background: #ffdf00;
    margin-bottom: 15px;
    height: 80px;
    text-align: center;
}

.counterBlock .display {

}

.filter-red {
    color: red;
    font-weight: bold;
}

.input-red {
    background-color: lightcoral;
}

.controlPanel {
    padding: 15px;
    border-radius: 5px;
    border: 2px solid;
    display: flex;
    justify-content: space-around;
}

.containerValue {
    padding: 5px;
    display: flex;
    justify-content: space-between;
}

.containerValue input {
    border-radius: 3px;
    width: 50%;
    text-align: center;
    font-weight: 700;
    font-size: 1em;
}

.numb {
    font-size: 40px;
}

